import axios from 'axios';
import React from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'

const Page = () => {
    const { register, formState: { errors }, handleSubmit } = useForm<formFields>();

    type formFields = {
        code: string,
        percent: string,
    }

    const onSubmit: SubmitHandler<formFields> = async (data) => {
        try {
            const res = await axios.post("http://localhost:5200/api/promocode/create", { data }, { withCredentials: true });

        } catch (err) {
            console.error(err);
        }
    }
    return (
        <div className='flex flex-col gap-7'>
            <h1 className='section-title'>Promocodes</h1>
            <form className='p-4 border-borderColor border-[1px] rounded-lg' onSubmit={handleSubmit(onSubmit)}>
                
            </form>
        </div>
    )
}

export default Page